///perfil/persona/:persona



export const updatePerfil = (deporte,personaId,token) => {
    return fetch(`https://api.klubo.club/api/perfil/persona/${personaId}`,{
            method: "PUT",
            headers: {
                Accept: 'application/json',
                "Content-type": "application/json",
                Authorization: `Bearer ${token}`
            },
            body: JSON.stringify(deporte)
        })
        .then(response => {
            console.log(response)
            return response.json()
        })
        .catch(err => {
            console.log(err)
        })
    }